.form-container {
  width: 100%;
 /*  box-shadow: 0 5px 8px 0 rgba(0, 0, 0, 0.2), 0 7px 20px 0 rgba(0, 0, 0, 0.2); */
  border-radius: 10px;
  display: grid;
  grid-template-columns: 3fr 2fr;
}

.close-btn {
  position: absolute;
  top: 2%;
  right: 3%;
  font-size: 1.5rem;
  z-index: 1;
  color: #fff;
  cursor: pointer;
}

.form-content-left {

  border-radius: 10px 0 0 10px;
  position: relative;
}

.form-img {
  width: 80%;
  height: 80%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.form-img-2 {
  width: 60%;
  height: 60%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}


/* .form.form-success-ok h1 {
  font-size: 22px;
  color: #7d1ef8;
} */

.form-content-right {
  border-radius: 0 10px 10px 0;
  position: relative;
}

.form-content-right-success {
  border-radius: 0 10px 10px 0;
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
}



.form {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.form h1 {
  font-size: 1rem;
  text-align: start;
  width: 80%;
  margin-bottom: 1rem;
  color: #fff;
}

 h1.form-success{
  text-align: center;
  font-size: 24px;
  color: #7d1ef8;
}


.form-inputs {
  margin-bottom: 0.5rem;
  width: 80%;
}

.form-inputs p {
  font-size: 0.8rem;
  margin-top: 0.5rem;
  color: #f00e0e;
}

.form-label {
  display: inline-block;
  font-size: 0.8rem;
  margin-bottom: 6px;
  color: black;
}

.form-input.textarea {
  height: 155px;
  /* border: none; */
}

.form-input {
  display: block;
  padding-left: 10px;
  outline: none;
  border-radius: 2px;
  height: 40px;
  width: 100%;
  /* border: none; */
}

.form-input::placeholder {
  color: #595959;
  font-size: 12px;
}

.form-input-btn {
  width: 80%;
  height: 50px;
  margin-top: 10px;
  border-radius: 2px;
  background: linear-gradient( 90deg, #3134e5 0%, #191a83 100% );
  outline: none;
  border: none;
  color: #fff;
  font-size: 1rem;
  transition: 0.5s;
}

.form-input-btn:hover {
  cursor: pointer;
  background: linear-gradient( 90deg, #191a83 0%, #3134e5 100% );
  transition: all 0.4s ease-out;
}

.form-input-login {
  font-size: 0.8rem;
  margin-top: 10px;
  color: #fff;
  width: 80%;
  text-align: center;
}

.form-input-login a {
  text-decoration: none;
  color: #27cdff;
  font-weight: 600;
}

.whastapp-btn{
  display: flex;
  align-items: center;
  padding-top: 15px;
}
.whastapp-btn a {
  text-decoration: none;
}

.content-btn{
    display: flex;
    border: 2px solid #771bec;
    border-radius: 25px;
    align-items: center;
    font-weight: 600;
    padding: 2px 20px;
}

.img-what img{
  width: 35px;
}


@media screen and (max-width: 768px){

  .form-content-left{
    text-align: center;
    padding-top: 22px;
  }

  .whastapp-btn{
    justify-content: center;
  }
  }